import React, { Suspense } from "react";
import AcademicDepartmentManagerProfile from "./AcademicDepartmentManagerProfile";
import UserMemberMainWrapper from "../../Layout/UserMemberTab/UserMemberMainWrapper";
import { useLocation, useSearchParams } from "react-router-dom";
import QvipleLoader from "../../Loader/QvipleLoader";
import InstituteAcademicDepartmentStaff from "../../InstituteMember/InstituteAcademicDepartment/InstituteAcademicDepartmentDetail/InstituteAcademicDepartmentStaff/InstituteAcademicDepartmentStaff";
import InstituteAcademicDepartmentStudent from "../../InstituteMember/InstituteAcademicDepartment/InstituteAcademicDepartmentDetail/InstituteAcademicDepartmentStudent/InstituteAcademicDepartmentStudent";
import InstituteAcademicDepartmentBatch from "../../InstituteMember/InstituteAcademicDepartment/InstituteAcademicDepartmentDetail/InstituteAcademicDepartmentBatch/InstituteAcademicDepartmentBatch";
import InstituteAcademicDepartmentMaster from "../../InstituteMember/InstituteAcademicDepartment/InstituteAcademicDepartmentDetail/InstituteAcademicDepartmentMaster/InstituteAcademicDepartmentMaster";
import InstituteAcademicDepartmentCls from "../../InstituteMember/InstituteAcademicDepartment/InstituteAcademicDepartmentDetail/InstituteAcademicDepartmentCls/InstituteAcademicDepartmentCls";
import { useInstituteUserDepartmentDashboard } from "../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import AcademicDepartmentManagerClass from "./AcademicDepartmentManagerClass";
import InstituteTheoryClassDetail from "../../InstituteMember/InstituteAcademicDepartment/InstituteDepartmentClass/InstituteTheoryClassDetail/InstituteTheoryClassDetail";
import InstitutePracticalBatchClassDetail from "../../InstituteMember/InstituteAcademicDepartment/InstituteDepartmentClass/InstitutePracticalBatchClassDetail/InstitutePracticalBatchClassDetail";

const AcademicDepartmentManager = () => {
  const getQuery = useLocation();
  const [searchParams] = useSearchParams();
  const { instituteUserDepartmentDashboard } =
    useInstituteUserDepartmentDashboard({
      data: {
        did: getQuery.state?.onClickValue?._id,
      },
      skip: !getQuery.state?.onClickValue?._id,
    });

  //   console.info("dgbjshdgs", getQuery.state);
  return (
    <>
      <UserMemberMainWrapper>
        <Suspense fallback={<QvipleLoader />}>
          {!searchParams.get("type") ? (
            <AcademicDepartmentManagerProfile />
          ) : searchParams.get("type") === "staff" ? (
            <UserMemberContainerWrapper>
              <InstituteAcademicDepartmentStaff
                did={getQuery.state?.onClickValue?._id}
                openAs="DEPARTMENT_HEAD"
              />
            </UserMemberContainerWrapper>
          ) : searchParams.get("type") === "student" ? (
            <UserMemberContainerWrapper>
              <InstituteAcademicDepartmentStudent
                did={getQuery.state?.onClickValue?._id}
                openAs="DEPARTMENT_HEAD"
              />
            </UserMemberContainerWrapper>
          ) : searchParams.get("type") === "batch" ? (
            <UserMemberContainerWrapper>
              <InstituteAcademicDepartmentBatch
                did={getQuery.state?.onClickValue?._id}
                openAs="DEPARTMENT_HEAD"
              />
            </UserMemberContainerWrapper>
          ) : searchParams.get("type") === "master" ? (
            <UserMemberContainerWrapper>
              <InstituteAcademicDepartmentMaster
                did={getQuery.state?.onClickValue?._id}
                instituteId={instituteUserDepartmentDashboard?.institute?._id}
                openAs="DEPARTMENT_HEAD"
                departmentName={instituteUserDepartmentDashboard?.dName ?? ""}
              />
            </UserMemberContainerWrapper>
          ) : searchParams.get("type") === "cls" ? (
            <UserMemberContainerWrapper>
              <InstituteAcademicDepartmentCls
                did={getQuery.state?.onClickValue?._id}
                instituteId={instituteUserDepartmentDashboard?.institute?._id}
                openAs="DEPARTMENT_HEAD"
              />
            </UserMemberContainerWrapper>
          ) : searchParams.get("type") === "cls-detail" ? (
            <UserMemberContainerWrapper>
              <AcademicDepartmentManagerClass
                did={getQuery.state?.onClickValue?._id}
                instituteId={instituteUserDepartmentDashboard?.institute?._id}
              />
            </UserMemberContainerWrapper>
          ) : searchParams.get("type") === "theory-detail" ? (
            <UserMemberContainerWrapper>
              <InstituteTheoryClassDetail
                clsId={getQuery.state?.clsId}
                instituteId={instituteUserDepartmentDashboard?.institute?._id}
                openAs="DEPARTMENT_HEAD"
              />
            </UserMemberContainerWrapper>
          ) : searchParams.get("type") === "practical-detail" ? (
            <UserMemberContainerWrapper>
              <InstitutePracticalBatchClassDetail
                clsId={getQuery.state?.clsId}
                instituteId={instituteUserDepartmentDashboard?.institute?._id}
                openAs="DEPARTMENT_HEAD"
              />
            </UserMemberContainerWrapper>
          ) : null}
        </Suspense>
      </UserMemberMainWrapper>
    </>
  );
};

export default AcademicDepartmentManager;
