import StaffMemberConfig from "../../../MembersTab/staff/Mainbody/StaffMemberConfig";
import { useLocation, useSearchParams } from "react-router-dom";
import AcademicDepartmentManager from "../AcademicDepartmentManager/AcademicDepartmentManager";
import DepartmentSiteInfoConfig from "../DepartmentManager/DepartmentSiteInfo/DepartmentSiteInfoConfig";
const AcademicDepartmentManagerConfig = () => {
  const location = useLocation();
  const [searchParams] = useSearchParams();
  return (
    <>
      {searchParams.get("a") === "site" ? (
        <DepartmentSiteInfoConfig did={location?.state?.onClickValue?._id} />
      ) : (
        <StaffMemberConfig
          index="1"
          sid={location?.state?.sid ?? ""}
          onClickValue={location?.state?.onClickValue ?? ""}
        >
          <AcademicDepartmentManager />
        </StaffMemberConfig>
      )}
    </>
  );
};

export default AcademicDepartmentManagerConfig;
